import React, { Suspense, lazy } from 'react';
import { Route, Switch } from 'react-router-dom';

const DashboardPage = lazy(() => import('../../views/pages/dashboard'));
const LoginPage = lazy(() => import('../../views/pages/login'));
const LaporanStockGlobalProduksiPage = lazy(() =>
  import('../../views/pages/laporan/stock_global_produksi/laporanstockglobalproduksi'),
);
const LaporanKirimSaldoTahunPage = lazy(() =>
  import('../../views/pages/laporan/kirim_saldo_tahunan/laporankirimsaldotahun'),
);
const NotFound = lazy(() => import('../../views/pages/notfoundpage'));

// eslint-disable-next-line import/prefer-default-export
export const RouteLink = () => {
  return (
    <Suspense fallback={<div />}>
      <Switch>
        <Route path="/amg-cadp-production" exact component={LoginPage} />
        <Route path="/amg-cadp-production/dashboard" component={DashboardPage} />
        <Route path="/amg-cadp-production/laporan/stock-global-produksi" component={LaporanStockGlobalProduksiPage} />
        <Route path="/amg-cadp-production/laporan/kirim-saldo-tahun" component={LaporanKirimSaldoTahunPage} />
        <Route path="*" component={NotFound} />
      </Switch>
    </Suspense>
  );
};
