import React, { useEffect } from 'react';
import { Layout, Menu, Breadcrumb, Row, Col } from 'antd';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation } from 'react-router-dom';
import './App.css';
import { HomeOutlined, ReadOutlined, SettingOutlined } from '@ant-design/icons';
import Loading from 'react-fullscreen-loading';
import ui from './application/selectors/ui';
import login from './application/selectors/login';
import { pageLoadedLogin } from './application/actions/ui';
import { RouteLink } from './infrastructure/router/RouteLink';
import { checkLogin } from './application/actions/login';
import breadcrumbNameMap from './infrastructure/router/breadcrumb-map';

function App() {
  const { Header, Footer, Content, Sider } = Layout;
  const { SubMenu } = Menu;
  const dispatch = useDispatch();
  const loading = useSelector(ui.getLoading);
  const isLogin = useSelector(login.getIsLogin);
  const today = new Date();
  const path = useLocation();
  const pathSnippets = path.pathname.split('/').filter((i) => i);
  const extrabreadcrumbItems = pathSnippets.map((_, index) => {
    const url = `/${pathSnippets.slice(0, index + 1).join('/')}`;
    return <Breadcrumb.Item>{breadcrumbNameMap[url]}</Breadcrumb.Item>;
  });
  const breadcrumbItems = [<Breadcrumb.Item key="home">App</Breadcrumb.Item>].concat(
    extrabreadcrumbItems,
  );

  useEffect(() => {
    dispatch(pageLoadedLogin);
  }, [dispatch]);

  useEffect(() => {
    dispatch(checkLogin);
  }, [dispatch]);
  if (loading) {
    return <Loading loading background="rgba(0, 0, 0, 0.39)" loaderColor="#3498db" />;
  }
  return !isLogin ? (
    <Layout style={{ height: '100vh' }}>
      <Header>
        <h3 style={{ color: 'white', textAlign: 'center', marginTop: 15 }}>CADP REPORT</h3>
      </Header>
      <Content style={{ height: '100vh' }}>
        <div className="site-layout-content">
          <RouteLink />
        </div>
      </Content>
      <Footer style={{ textAlign: 'center' }}>D Design ©2021 Created by MDN</Footer>
    </Layout>
  ) : (
    <Layout style={{ minHeight: '100vh' }}>
      <Sider collapsible>
        <div className="logo">
          <h5 style={{ color: 'white', textAlign: 'center' }}>CADP REPORT</h5>
        </div>
        <Menu
          theme="dark"
          defaultSelectedKeys={[path.pathname]}
          mode="inline"
          defaultOpenKeys={path.pathname.includes('laporan') ? ['laporan'] : ''}
        >
          <Menu.Item key="/dashboard" icon={<HomeOutlined />}>
            <a href="/amg-cadp-production/dashboard" className="link-menus">
              Dashboard
            </a>
          </Menu.Item>
          <SubMenu key="laporan" icon={<ReadOutlined />} title="Laporan">
            <Menu.Item key="/laporan/stock-global-produksi">
              <a href="/amg-cadp-production/laporan/stock-global-produksi" className="link-menus">
                Stock Global Produksi
              </a>
            </Menu.Item>
            <Menu.Item key="/laporan/kirim-saldo-tahun">
              <a href="/amg-cadp-production/laporan/kirim-saldo-tahun" className="link-menus">
                Kirim dan Saldo per Tahun
              </a>
            </Menu.Item>
          </SubMenu>
        </Menu>
      </Sider>
      <Layout className="site-layout">
        <Header className="site-layout-background" style={{ padding: 0, paddingRight: 10 }}>
          <Row gutter={{ xs: 8, sm: 16 }}>
            <Col className="gutter-row" offset={15} style={{ paddingLeft: 100 }}>
              <span style={{ fontSize: 12, textAlign: 'right' }}>
                Tanggal Hari Ini : {today.toLocaleDateString('id-ID')}
              </span>
            </Col>
            <Col span={4} style={{ paddingLeft: 35 }}>
              <Menu mode="horizontal">
                <SubMenu key="SubMenu" icon={<SettingOutlined />} title="Pengaturan Akun">
                  <Menu.ItemGroup title="Pengaturan">
                    <Menu.Item key="setting:1">Akun</Menu.Item>
                    <Menu.Item key="setting:2">
                      <a
                        href="/amg-cadp-production"
                        className="link-menus"
                        onClick={() => localStorage.clear()}
                      >
                        Log Out
                      </a>
                    </Menu.Item>
                  </Menu.ItemGroup>
                </SubMenu>
              </Menu>
            </Col>
          </Row>
        </Header>
        <Content style={{ margin: '0 16px' }}>
          <Breadcrumb style={{ margin: '16px 0' }}>{breadcrumbItems}</Breadcrumb>
          <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
            <RouteLink />
          </div>
        </Content>
        <Footer style={{ textAlign: 'center' }}>D Design ©2021 Created by MDN</Footer>
      </Layout>
    </Layout>
  );
}

export default App;
